import React, { useState, useEffect } from 'react' 
import Logo from '../assets/logo.svg'
import MetaMaskGray from '../assets/metamask-gray.svg'
import { default as MetaMaskColor } from '../assets/metamask-color.svg'
import LoginDivider from '../assets/login-divider.svg'
import WalletConnectColor from '../assets/wallet-connect-color.svg'
import WalletConnectGray from '../assets/wallet-connect-gray.svg'
import moment from "moment"
import { ethers } from "ethers";
import axios from 'axios';
import PuffLoader from "react-spinners/DotLoader";
import converter from "hex2dec";
import WalletConnect from "@walletconnect/client";
import WalletConnectProvider from "@walletconnect/web3-provider";
import Web3 from 'web3';
import QRCodeModal from "@walletconnect/qrcode-modal";
import { convertUtf8ToHex } from "@walletconnect/utils";



import BummerImg from '../assets/bummer-img.svg'



import { providers } from "ethers";

const Login = () => {

    const[metamaskColor, setMetamaskColor] = useState(false);
    const[walletConnectColor, setWalletConnectColor] = useState(false);
    const[loading, setLoading] = useState(false);
    const[rawMessage, setRawMessage] = useState("");   
    const[message, setMessage] = useState("");
    const[is404, setIs404] = useState(false)
    const[authID, setAuthID] = useState(null)
    const[serviceID, setServiceID] = useState(null)
    const[serviceName, setServiceName] = useState('')

    useEffect(() => {        
        async function doFetch(){
            let id = window.location.href.split("/")[5]                        
            let hash = window.location.href.split("/")[4]               
            if(hash !== "#" || !id){
                setIs404(true)
                return
            }
            let endpoint=`https://connect.api.nftylabs.argonx.com/connect/${id}`              
            await axios.get(endpoint)
                        .then((response) => {
                            console.log("Response", response)
                            setRawMessage(response.data.payload);
                            let rawMessage = response.data.payload; 
                            if(rawMessage){
                                if(rawMessage.code === 403 || rawMessage.code == 404 ){
                                    setIs404(true)
                                    return
                                }                                
                            }                                    
                            let authID = JSON.stringify(rawMessage.ID).toUpperCase()      
                            let authIDHex = converter.decToHex(authID, { prefix: false }).toUpperCase()                                          
                            let message = `NFTY Labs signed message v0${rawMessage.Version}\n`+
                                          `You are authenticating to: ${rawMessage.Service}\n`+
                                          `Authorization ID: ${authIDHex}\n`+
                                          `Void after: ${ moment.utc(rawMessage.Until).format("ddd MMM D HH:mm:ss YYYY")} UTC\n`
                            console.log(message);
                            setMessage(message);
                            setAuthID(authIDHex);
                            setServiceName(rawMessage.Service);
                            setServiceID(id);
                        })
                        .catch(err => console.error("Error occurred", err))
        }
        doFetch();
    }, []);

    const verifySignature = async (signedMessage, account) => {
        const endpoint='https://connect.api.nftylabs.argonx.com/verify';
            await axios.post(endpoint, {
                id: parseInt(authID, 16),
                serviceID: serviceID,
                signature: signedMessage,
                address: account
            }).then((response) => {
                console.log("Verified, data is", response.data)
                if(response.data.link){
                    window.location.href = response.data.link
                }
                else{
                    if (response.data.code === 403) {
                      alert("Not authorized. Did you pick the wrong account?");
                    } else {
                      alert("Service currently unavailable. Try again later.");
                    }
                }                            
            }).catch(err => {
                console.error("Error", err)
                alert( "Not authorized, did you pick the wrong account?")
            }); 
    }

    const connectMetamaskWalletAndSign = async  () => {        
       try{           
            await window.ethereum.enable()
            let account;
            const provider = await new ethers.providers.Web3Provider(window.ethereum)                    
            account = await provider.listAccounts().then(result => {
            let accountFound = result[0];
            return accountFound
            });
            const signer = provider.getSigner()
            const signedMessage = await signer.signMessage(message)
            setLoading(true)
            //verify the signature
            await verifySignature(signedMessage, account)           
       }catch(e){
            console.log("Metamask connect error");
            alert("Unable to connect to metamask wallet. Please try again later")
       }
       setLoading(false)
    }

    const connectWallet = async (accountFound, connector) => {
                
            setLoading(true);
            try{                                    
                const msgParams = [
                     convertUtf8ToHex(message),
                     accountFound                                        
                ];
         
                 // Sign personal message
                 let signedMessage = await connector.signPersonalMessage(msgParams).then((result) => {
                     // Returns signature.        
                     return result
                 })
                 .catch(error => {
                 // Error returned when rejected
                 console.error(error);             
                 alert("Error occurred when signing message")
                 })
         
                 console.log("signedMessage", signedMessage)
                 await verifySignature(signedMessage, accountFound)   
                 
            }catch(e){
                console.log(e)
                alert("Issue when connecting to WalletConnect")
            }           
            setLoading(false)  
    }

    const connectWalletConnectAndSign = async  () => {     
        
        //  Create WalletConnect Provider   
        const connector = new WalletConnect({
            bridge: "https://bridge.walletconnect.org", // Required
            qrcodeModal: QRCodeModal,
        });
        let accountFound;
        // Check if connection is already established
        if (!connector.connected) {
            // create new session
            connector.createSession();
        }
        else{
            console.log(connector)
            accountFound = connector._accounts[0]
            connectWallet(accountFound, connector)
        }
        
        // Subscribe to connection events
        connector.on("connect", async (error, payload) => {
            if (error) {
            throw error;
            }
            // Get provided accounts and chainId
            const { accounts, chainId } = payload.params[0];
            accountFound = accounts[0]
            connectWallet(accountFound, connector) 
            console.log("connected")
        });
        
        connector.on("session_update", (error, payload) => {
            if (error) {
            throw error;
            }
        
            // Get updated accounts and chainId
            const { accounts, chainId } = payload.params[0];
            accountFound = accounts[0]
            connectWallet(accountFound, connector) 
            console.log("session updated")
        });
        
        
        connector.on("disconnect", (error, payload) => {
            console.log("session disconnected")
            if (error) {
            throw error;
            }
            // Delete connector
            localStorage.removeItem("walletconnect")     
            window.location.reload()                           
        });
		
             

     }

    if(is404){
        return(
            <div class="flex h-screen">
            <div class="m-auto flex flex-col">
            <p className="label-text font-3xl font-bold text-black mb-4">Oh No! It seems that the link you entered is invalid. Could you check again?</p>
                <img src={BummerImg} />                
            </div>
            </div>
        )                
    }


    if(loading){
        return(            
            <div class="flex h-screen flex-col">
            <div class="m-auto">                
                    <PuffLoader loading={loading} size={90} />                    
            </div>            
            <p className="label-text font-sm text-black mt-8">Taking too long? .... Please refresh the screen</p>
            </div>
        )
    }

    return (
        <div className="flex justify-center items-center animate-fade-in-down">


            <div className="flex shadow-2xl rounded-2xl flex-col md:h-1/2 md:w-3/12 md:my-12 md:mb-12  lg:h-1/2 lg:w-3/12 lg:my-24 lg:mb-12 justify-center items-center">        
                   
                    <img alt="" className="my-2 h-20 mb-4" src={Logo}/> 
                                                                    
                    <div className="my-4 md:w-full w-1/2 flex justify-center items-center">
                            <p className="label-text text-black">Login to {serviceName}</p>
                    </div>  
                    
            
                       <div className="flex items-center justify-center flex-col">
                            <div className="h-14 mb-12">
                                    <img alt="" 
                                    className={metamaskColor? "animate-fade-in-logo cursor-pointer":"cursor-pointer"}

                                    onMouseEnter={() => setMetamaskColor(true)}
                                    onMouseLeave={() => setMetamaskColor(false)}
                                    src={ metamaskColor ? MetaMaskColor : MetaMaskGray}
                                    onClick={()=>connectMetamaskWalletAndSign()}
                                    />
                             </div>
                                
                                
                             <div className="my-8 border border-gray-500 cursor-pointer w-1/2 flex justify-center items-center">
                                <p className="label-text text-gray-500 font-bold"
                                onClick={()=>connectMetamaskWalletAndSign()}>Metamask</p>
                            </div>                                      
                            
                                                            
                            <img alt="" className="mt-4 mb-8"  src={LoginDivider} />
                            
                            <div className="h-14 lg:mb-4 md:mb-4">
                                <img alt="" 
                                className={walletConnectColor ? "animate-fade-in-logo cursor-pointer":"cursor-pointer"}
                                onMouseEnter={() => setWalletConnectColor(true)}
                                onMouseLeave={() => setWalletConnectColor(false)}
                                src={ walletConnectColor ? WalletConnectColor : WalletConnectGray}
                                onClick = {() => connectWalletConnectAndSign()}
                                />
                            </div>
                            
                            <div className="my-9 border border-gray-500 cursor-pointer w-1/2 flex justify-center items-center">
                                <p onClick = {()=>connectWalletConnectAndSign()} className="label-text text-gray-500 font-bold">WalletConnect</p>
                            </div>               
                       </div>
               
            </div>

        </div>
    );
}

export default Login;
//https://www.npmjs.com/package/@walletconnect/browser?activeTab=readme